






import {Component, Vue} from 'vue-property-decorator';
@Component({
    name: 'Tip'
})
export default class Tip extends Vue{
    name:any = null;
    isShow:boolean = false
    warning(name:any) {
        this.name = name;
        this.isShow = true;
        setTimeout(()=>{
            this.isShow = false;
        },2000)
    }
}
